import React from 'react';
import T from 'prop-types';
import styled from 'styled-components';

const OptionContainer = styled.div`
    padding: 10px 15px 10px 15px;
`;
const OptionLabel = styled.div`
    padding-bottom: 3px;
    font-weight: 500;
    font-size: 13px;
`;
const OptionContent = styled.div``;

const Option = ({ label, children, depends, 'data-test': dataTestId }) => {
    if (!depends()) {
        return null;
    }
    return (
        <OptionContainer data-test="option" data-test-id={dataTestId}>
            <OptionLabel data-test="option-label">{label}</OptionLabel>
            <OptionContent data-test="option-content">{children}</OptionContent>
        </OptionContainer>
    );
};

Option.propTypes = {
    label: T.string,
    children: T.any,
    depends: T.func,
    'data-test': T.string,
};

Option.defaultProps = {
    depends: () => true,
};
export default Option;
